.chessboard-wrapper {
    display: flex;
    flex-direction: column;
    justify-content: center;
  
    overflow: hidden;
    width: 100%;
    max-width: 400px;
    margin: 0 auto;
    position: relative;
    aspect-ratio: 1;
  }
  
  .chessboard-wrapper > div {
    border: 1px solid #ccc;
    border-radius: 8px;
    overflow: hidden;
    width: 100% !important;
    height: 100% !important;
  }

  .chessboard-wrapper > div > div {
    width: 100% !important;
    height: 100% !important;
  }
 