.login-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  background-color: white;
  padding: 2rem;
  box-sizing: border-box;
}

.welcome-message {
  color: #000;
  font-size: 20px;
  font-weight: 600;
  text-align: center;
  margin-bottom: 3rem;
  max-width: 400px;
}

.login-form {
  background: transparent;
  padding: 0;
  border-radius: 0;
  box-shadow: none;
  width: 100%;
  max-width: 400px;
}

.login-logo {
  text-align: center;
  margin-bottom: 1rem;
}

.login-logo .logo-img {
  height: 80px;
  width: auto;
  object-fit: contain;
}

.form-group {
  margin-bottom: 1rem;
}

.form-label {
  display: block;
  margin-bottom: 0.5rem;
  color: #555;
  font-weight: 500;
}

.form-input {
  width: 100%;
  padding: 1rem;
  border: 1px solid #ddd;
  border-radius: 8px;
  font-size: 16px;
  box-sizing: border-box;
  background: white;
  transition: border-color 0.2s, box-shadow 0.2s;
}

.form-input:focus {
  outline: none;
  border-color: #2f74ab;
  box-shadow: 0 0 0 2px rgba(47, 116, 171, 0.2);
}

.login-button {
  width: 100%;
  padding: 1rem;
  background-color: #2f74ab;
  color: white;
  border: none;
  border-radius: 8px;
  font-size: 16px;
  font-weight: 600;
  cursor: pointer;
  margin-bottom: 1rem;
  transition: background-color 0.2s, transform 0.1s;
}

.login-button:hover:not(:disabled) {
  background-color: #255d89;
  transform: translateY(-1px);
}

.login-button:disabled {
  background-color: #bdc3c7;
  cursor: not-allowed;
}

.guest-link-container {
  text-align: center;
  margin-top: 1.5rem;
}

.guest-link {
  background: none;
  border: none;
  color: #2f74ab;
  font-size: 14px;
  cursor: pointer;
  text-decoration: underline;
  padding: 0.5rem;
  transition: color 0.2s;
}

.guest-link:hover:not(:disabled) {
  color: #255d89;
}

.guest-link:disabled {
  color: #bdc3c7;
  cursor: not-allowed;
}

.forgot-password-link {
  display: block;
  width: 100%;
  text-align: right;
  margin-top: 0.5rem;
  margin-bottom: 1.5rem;
  color: #2f74ab;
  font-size: 14px;
  background: none;
  border: none;
  padding: 0;
  text-decoration: underline;
  cursor: pointer;
  font-family: inherit;
}

.forgot-password-link:hover {
  color: #255d89;
}

.error-message {
  color: #e74c3c;
  text-align: center;
  margin-bottom: 1rem;
  padding: 1rem;
  background-color: #fdf2f2;
  border: 1px solid #fecaca;
  border-radius: 8px;
  font-weight: 500;
}

