.statistics-page {
  max-width: 400px;
  margin: 0 auto;
  width: 100%;
}

.stats-header {
  text-align: center;
  margin-bottom: 2rem;
}

.stats-header h1 {
  font-size: 1.5rem;
  color: #333;
  margin-bottom: 0.5rem;
  font-weight: 600;
}

.rank-badge {
  display: flex;
  flex-direction: column;
  align-items: center;
  background: linear-gradient(135deg, #2f74ab 0%, #1e5a8a 100%);
  color: white;
  padding: 0.5rem 1rem;
  border-radius: 12px;
  margin: 0 auto;
  max-width: 200px;
}

.rank-text {
  font-weight: 600;
  font-size: 0.9rem;
}

.xp-text {
  font-size: 0.8rem;
  opacity: 0.9;
}

.overall-stats {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 1rem;
  margin-bottom: 2rem;
}

.stat-card {
  background: white;
  border-radius: 12px;
  padding: 1rem;
  text-align: center;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
  border: 1px solid #f0f0f0;
}

.stat-value {
  font-size: 1.5rem;
  font-weight: 700;
  color: #2f74ab;
  margin-bottom: 0.25rem;
}

.stat-label {
  font-size: 0.8rem;
  color: #666;
  font-weight: 500;
}

.achievements-section {
  margin-bottom: 2rem;
}

.achievements-section h2 {
  font-size: 1.2rem;
  color: #333;
  margin-bottom: 1rem;
  font-weight: 600;
}

.achievements-grid {
  display: flex;
  flex-direction: column;
  gap: 0.75rem;
}

.achievement {
  display: flex;
  align-items: center;
  background: white;
  border-radius: 12px;
  padding: 1rem;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
  border: 1px solid #f0f0f0;
}

.achievement-icon {
  font-size: 2rem;
  margin-right: 1rem;
}

.achievement-text {
  flex: 1;
}

.achievement-title {
  font-size: 0.9rem;
  color: #333;
  font-weight: 600;
  margin-bottom: 0.25rem;
}

.achievement-value {
  font-size: 0.8rem;
  color: #666;
}

.theme-stats-section h2 {
  font-size: 1.2rem;
  color: #333;
  margin-bottom: 1rem;
  font-weight: 600;
}

.theme-stats-grid {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.theme-card {
  background: white;
  border-radius: 12px;
  padding: 1rem;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
  border: 1px solid #f0f0f0;
}

.theme-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 1rem;
}

.theme-header h3 {
  font-size: 1rem;
  color: #333;
  margin: 0;
  font-weight: 600;
}

.theme-level {
  font-size: 0.9rem;
  color: #ffa726;
}

.theme-progress {
  margin-bottom: 1rem;
}

.progress-bar {
  width: 100%;
  height: 8px;
  background-color: #f0f0f0;
  border-radius: 4px;
  overflow: hidden;
  margin-bottom: 0.5rem;
}

.progress-fill {
  height: 100%;
  background: linear-gradient(90deg, #2f74ab 0%, #4caf50 100%);
  border-radius: 4px;
  transition: width 0.3s ease;
}

.progress-text {
  font-size: 0.8rem;
  color: #666;
  text-align: center;
}

.theme-metrics {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}

.metric {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.metric-label {
  font-size: 0.8rem;
  color: #666;
}

.metric-value {
  font-size: 0.8rem;
  font-weight: 600;
  color: #333;
}

/* Responsive adjustments */
@media (max-width: 380px) {
  .overall-stats {
    grid-template-columns: 1fr;
  }
  
  .stat-value {
    font-size: 1.3rem;
  }
  
  .achievement-icon {
    font-size: 1.5rem;
    margin-right: 0.75rem;
  }
}