
.user-info-section {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  gap: 16px;
  padding: 32px 20px;
  border-bottom: 1px solid #eee;
}

.user-avatar-large {
  width: 80px;
  height: 80px;
  border-radius: 50%;
  background: #2f74ab;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 32px;
  font-weight: 600;
  color: white;
  /* box-shadow: 0 8px 24px rgba(47, 116, 171, 0.3); */
}

.user-details-section h2 {
  margin: 0 0 8px 0;
  font-size: 24px;
  color: #333;
  font-weight: 600;
}

.username {
  margin: 0 0 12px 0;
  font-size: 16px;
  color: #666;
}

.role-badge {
  display: inline-block;
  padding: 4px 12px;
  border-radius: 12px;
  font-size: 12px;
  font-weight: 500;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.role-admin {
  background-color: #e74c3c;
  color: white;
}

.role-user {
  background-color: #3498db;
  color: white;
}

.role-guest {
  background-color: #95a5a6;
  color: white;
}

.profile-navigation {
  display: flex;
  flex-direction: column;
  gap: 12px;
  margin-bottom: 32px;
}

.nav-item-profile {
  display: flex;
  align-items: center;
  gap: 16px;
  padding: 16px 20px;
  cursor: pointer;
  border-radius: 12px;
  transition: all 0.2s ease;
  background: #f8f9fa;
  border: 1px solid transparent;
}

.nav-item-profile:hover {
  background: #e9ecef;
  border-color: #2f74ab;
  transform: translateY(-1px);
}

.nav-icon-profile {
  width: 24px;
  height: 24px;
  fill: #2f74ab;
  flex-shrink: 0;
}

.nav-label-profile {
  font-size: 16px;
  font-weight: 500;
  color: #333;
}

.logout-btn {
  padding: 16px;
  background: #dc3545;
  color: white;
  border: none;
  border-radius: 12px;
  font-size: 16px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.2s ease;
  margin: 0 20px;
}

.logout-btn:hover {
  background: #c82333;
  transform: translateY(-1px);
}